}

.moveInRL {
  -webkit-animation: moveInRL 0.5s linear 0s 1 normal none;
          animation: moveInRL 0.5s linear 0s 1 normal none;
}

@-webkit-keyframes moveInLR {
  from {left: -100%;}
  to { left : 0}
}

@keyframes moveInLR {
  from {left: -100%;}
  to { left : 0}
}


@-webkit-keyframes moveInRL {
  from {left: 100%;}
